@charset "utf-8";

// Our variables
$base-font-family: 'neue-haas-unica', 'Helvetica Neue', Helvetica, Arial, sans-serif;
$header-font-family: omnium, sans-serif;
$base-font-size: 16px;
$base-font-weight: 300;
$small-font-size: $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit: 30px;

$text-color: #fff;
$frame-color: #333;
$background-color: #222;
$brand-color: #60B3BE;
$secondary-color: #52439B;

$selection-color: #FEF081;
$sidebar-hover-color: #7D5FA9;

$grey-color: #424242;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark: darken($grey-color, 25%);

// Width of the content area
$content-width: 800px;

$on-palm: 600px;
$on-laptop: 800px;
$on-xs: 575.98px;
$on-sm: 767.98px;
$on-md: 991.98px;
$on-lg: 1199.98px;

// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}

// Import partials from `sass_dir` (defaults to `_sass`)
@import "base", "fonts", "syntax-highlighting", "simple-sidebar", "pages";
